.container{
   background-color:#161616 ;
   height: 100vh;
   position: fixed;
   text-align: center;
   border-right: 3px solid #ff8c00;
   box-shadow: 0 0 16px #ff8c00;
}

.logo{
   height: 90px; 
   margin: 10px;
}

.menuu{
   height: 50%;
   /*
   padding: 20px;
   position: absolute;
   display: fixed;
   */
}

.link{
   text-decoration: none;

   .link-text{
      color: #ff8c00;
   }
   
}

.link:hover{
      
   .link-text{
      color: red;
   }
  
   .link-icon{
      color: #ff8c00;
   }
}

/*        Prove  
.link:after{content: 'PROVA';}  

*/

.link-text:active,.link-icon:active{
   color: blue;
}
.main{
   justify-content: center;
   align-items: center;
   display:flex;
   margin-left: 110px;
   height: 100vh;
   background-color:#161616;
   color:#ff8c00;
   .lav{
     height: 160px;
   }
}  



.card{ 
   border: white solid 3px;
   
   border-radius: 16%;
   margin: 7px;

   /*     Rivedere      */
   justify-content: center;
   align-items: center;
   display: flex;
}
.card:hover{
   scale: 116%;
   border-radius: 16%;
}




/* If the screen size is 601px or more, set the font-size of <div> to 80px */
@media only screen and (min-width: 601px) {
   .cardText {
     font-size: 36px;
     text-align: center;
   }
   .cardImage{
      height: 66px;
      display: flex;
      padding: 7px;
      /*border-bottom: white solid 3px;*/
   }

}



