  width:fit-content;
}

.home{
  margin-left: 110px;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: #000000;
}

.nome{color:#ff8c00;}
.video{display: flex;height: 100vh; }

.tag-top{
  margin-top: 6px;
  margin-left: 126px;
  font-size: x-large;
  position: absolute;
  color:#ff8c00
}

.tag-bottom{ 
  font-size: x-large;
  position: absolute;
  bottom: 6px;
  right: 20px;
  color:#ff8c00
}

span:hover{
  color: red;
  scale: 116%;
  .paw{
    color:#ff8c00;
  }
}

